* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.country__item {
  display: flex;
  align-items: center;
  margin-bottom: 5px;
  margin: 5px;
}
.country-list {
  list-style: none;
  padding: 0;
}
p {
  margin: 5px;
}
img {
  margin-right: 10px;
  border: 1px solid black;
}
.header {
  display: flex;
  align-items: center;
  margin-bottom: 5px;
  margin: 5px;
}
